import React, { useState } from 'react';
import { Checkbox } from 'antd';

export const CheckBoxMenu = props => {
  const [checked, setChecked] = useState(props.checked);
  const updateMenuRole = async () => {
    props.changeMenu(props.record);
    setChecked(!checked);
  };

  return (
    <div>
      <Checkbox checked={checked} onChange={updateMenuRole} />
    </div>
  );
};



