import React, { Component, Fragment } from 'react';
import { withRouter, Link } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew, BtnSave } from 'components/common/button';
import { ColEditDelete } from 'components/common';
import { Table, Card, Row, Col, Spin, Checkbox, message } from 'antd';
import to from 'await-to-js';

import {
  getModuleById,
  getMenuByModuleId,
  addNewUserInRole,
  deleteUser,
  updateMenu,
  deleteRole,
} from 'services/roleModule';
import { getAllUser } from 'services/managementUser';
import SelectAjax from 'components/common/input/SelectAjax';
import { getQueryParams } from 'utils/urlUtil';
import { CheckBoxMenu } from './CheckBoxMenu';

import './styles.scss';

class ForManagementUser extends Component {
  state = {
    dataTableMenu: [],
    dataTableRole: [],
    menuByModule: [],
    loading: false,
    idsUser: [],
    currentRole: {},
    loadingTableMenu: false,
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();

    if (queryParamId) {
      this.setState({
        moduleId: queryParamId,
      });
      this.getDataModuleById(queryParamId);
    }
  };

  getDataModuleById = async id => {
    const [error, response] = await to(getModuleById(id));
    if (error) return;
    this.setState({
      dataTableMenu: response.data.menus,
      dataTableRole: response.data.roles,
    });
    const menuLevel1 = response.data.menus.filter(menu => menu.level === 1);
    const menuLevel2 = response.data.menus.filter(menu => menu.level === 2);

    const dataTableMenu = menuLevel1.map(menu1 => {
      const menuLv2 = menuLevel2.filter(menu2 => menu1.menuId === menu2.parentId);
      const formatMenulv2 = menuLv2
        .map(item => ({ ...item, key: item.moduleId }))
        .sort((a, b) => a.ordering - b.ordering);
      const formatMenu = {
        ...menu1,
        key: menu1.moduleId,
        children: formatMenulv2,
      };
      return formatMenu;
    });
    this.setState({
      dataTableMenu,
    });
  };

  getInfoMenuByRole = async record => {
    const { dataTableMenu } = this.state;
    this.setState({ loadingTableMenu: true });
    const [error, response] = await to(getMenuByModuleId(record.roleId, record.moduleId));

    if (error) {
      this.setState({ loadingTableMenu: false });
      return;
    }
    const dataTable = [...dataTableMenu];
    this.setState(
      {
        menuByModule: response.data.menus,
        userByModule: response.data.users,
        currentRole: record,
        listMenuCheck: response.data.menus.map(item => item.menuId),
        dataTableMenu: dataTable,
      },
      () => this.setState({ loadingTableMenu: false })
    );
  };

  inputAddUser = value => {
    this.setState({
      idsUser: value,
    });
  };

  addNewUserModule = async () => {
    const { idsUser, currentRole } = this.state;
    const [error, response] = await to(
      addNewUserInRole(currentRole.roleId, currentRole.moduleId, idsUser)
    );
    if (error) return;
    if (response) {
      message.success('Thêm user thành công');
      this.setState({ idsUser: [] });
      this.getInfoMenuByRole(currentRole);
    }
  };

  confirmDelete = async id => {
    const { currentRole } = this.state;
    const [error, response] = await to(deleteUser(currentRole.roleId, currentRole.moduleId, id));
    if (error) return;
    if (response) {
      message.success('Xóa user thành công');
      this.getInfoMenuByRole(currentRole);
    }
  };

  changeMenuRole = value => {
    const { listMenuCheck } = this.state;
    const dataMenuCheck = [...listMenuCheck];
    const indexMenu = dataMenuCheck.indexOf(value.menuId);
    if (indexMenu !== -1) {
      dataMenuCheck.splice(indexMenu, 1);
    } else {
      dataMenuCheck.push(value.menuId);
    }
    this.setState({
      listMenuCheck: dataMenuCheck,
    });
  };

  editMenuRole = async () => {
    const { currentRole, listMenuCheck } = this.state;
    const [error, response] = await to(
      updateMenu(currentRole.roleId, currentRole.moduleId, listMenuCheck)
    );
    if (error) return;
    if (response) {
      message.success('Chỉnh sửa menu thành công');
      this.setState({ idsUser: [] });
      this.getInfoMenuByRole(currentRole);
    }
  };

  confirmDeleteRole = async (e, record) => {
    // const { currentRole } = this.state;
    const { moduleId } = this.state;
    const [error] = await to(deleteRole(record.moduleId, record.roleId));
    if (error) return;
    // if (currentRole.roleId === record.roleId) {
    this.setState({
      currentRole: {},
    });
    // }
    this.getDataModuleById(moduleId);
  };

  classNameCurrent = record => {
    const { currentRole } = this.state;
    if (currentRole.roleId === record.roleId) {
      return 'current-role-click';
    }
    return '';
  };

  render() {
    const {
      idsUser,
      dataTableMenu,
      dataTableRole,
      userByModule,
      menuByModule,
      moduleId,
      loadingTableMenu,
      currentRole,
    } = this.state;
    const sortTableMenu = dataTableMenu.sort((a, b) => a.ordering - b.ordering);
    const columnsMenu = [
      {
        title: 'Tên menu',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Action',
        render: (index, item) => {
          const checked = menuByModule.filter(menu => menu.menuId === item.menuId);
          const checkedItem = checked.length > 0;
          // if (!item.children) return <Checkbox checked={checked} />;
          // return '';
          return (
            <CheckBoxMenu checked={checkedItem} record={item} changeMenu={this.changeMenuRole} />
          );
        },
      },
    ];

    const columnsUser = [
      {
        title: 'Tên ',
        dataIndex: 'fullName',
        key: 'fullName',
      },
      {
        title: 'Email',
        dataIndex: 'email',
        key: 'email',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <Fragment>
            {!currentRole.isAdmin && (
              <ColEditDelete
                // linkEdit={{
                //   to: {
                //     pathname: '/management-user/form/edit/',
                //     search: `?id=${record.id}`,
                //   },
                // }}
                onConfirmDelete={() => {
                  this.confirmDelete(record.id);
                }}
              />
            )}
          </Fragment>
        ),
      },
    ];

    const columnsRole = [
      {
        title: 'Phân quyền',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Phân hệ',
        dataIndex: 'moduleName',
        key: 'moduleName',
      },
      {
        title: 'Admin',
        dataIndex: 'level',
        key: 'level',
        render: (index, item) => <Checkbox checked={item.isAdmin} />,
      },
      {
        title: 'Khóa',
        dataIndex: 'disabled',
        key: 'disabled',
        render: (index, item) => <Checkbox checked={item.disabled} />,
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <Fragment>
            {!record.isAdmin && (
              <ColEditDelete
                linkEdit={{
                  to: {
                    pathname: '/management-user-role/form',
                    search: `?moduleId=${record.moduleId}&roleId=${record.roleId}`,
                  },
                }}
                onConfirmDelete={e => this.confirmDeleteRole(e, record)}
              />
            )}
          </Fragment>
        ),
      },
    ];
    return (
      <Card>
        <Spin spinning={this.state.loading}>
          <Row>
            <Col span={24}>
              <Link to={`/management-user-role/form?moduleId=${moduleId}`}>
                <BtnAddNew />
              </Link>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table
                loading={this.state.loading}
                dataSource={dataTableRole}
                columns={columnsRole}
                onRow={record => {
                  return {
                    onClick: () => {
                      this.getInfoMenuByRole(record);
                    }, // click row
                  };
                }}
                rowClassName={this.classNameCurrent}
                rowKey="id"
              />
            </Col>
          </Row>
          <Row>
            <Col span={12}>
              {currentRole.moduleId && (
                <div style={{ marginTop: 4 }}>
                  <div>
                    <BtnSave
                      title="Lưu menu"
                      onClick={this.editMenuRole}
                      style={{ marginLeft: 10 }}
                    />
                  </div>
                  {!loadingTableMenu && (
                    <Table
                      dataSource={sortTableMenu}
                      columns={columnsMenu}
                      pagination={false}
                      style={{ padding: 5 }}
                      rowKey="menuId"
                    />
                  )}
                </div>
              )}
            </Col>
            <Col span={12}>
              {currentRole.moduleId && (
                <Fragment>
                  <div>
                    <SelectAjax
                      style={{ width: '70%' }}
                      mode="multiple"
                      keyValue="id"
                      disabled={currentRole.isAdmin}
                      keyLabel="userName"
                      setLabel={user => `${user.firstName} ${user.lastName}`}
                      value={idsUser}
                      service={getAllUser}
                      params={{ userTypeId: 3, disabled: false }}
                      onChange={this.inputAddUser}
                    />
                    <BtnAddNew
                      disabled={currentRole.isAdmin}
                      onClick={this.addNewUserModule}
                      style={{ marginLeft: 10 }}
                    >
                      Thêm User
                    </BtnAddNew>
                  </div>
                  <Table
                    loading={loadingTableMenu}
                    dataSource={userByModule}
                    columns={columnsUser}
                    style={{ padding: 5 }}
                    rowKey="id"
                    pagination={false}
                  />
                </Fragment>
              )}
            </Col>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <ForManagementUser {...props} />
  </Page>
));



