import { request } from 'utils';

const prefix = '/api/usermanagement';

export const getAllUser = params =>
  request({
    prefix,
    url: '/User',
    params: {
      ...params,
      isPagingEnabled: false,
    },
    method: 'GET',
  });

export const getUserById = (id, dataEdit) =>
  request({
    prefix,
    url: `/User/${id}`,
    params: {
      ...dataEdit,
    },
    method: 'GET',
  });

export const createUser = newUser =>
  request({
    prefix,
    url: `/User`,
    data: {
      ...newUser,
    },
    method: 'POST',
  });

export const updateUser = (id, dataUser) =>
  request({
    prefix,
    url: `/User/${id}`,
    data: {
      ...dataUser,
    },
    method: 'PUT',
  });

export const deleteUser = id =>
  request({
    prefix,
    url: `/User/${id}`,
    method: 'DELETE',
  });



